@charset "UTF-8";
.new-design2020 {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }
  .new-design2020 .list-of-clients {
    margin-top: 16px;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    align-items: center;
    justify-content: space-around;
    padding-left: 0;
    margin-bottom: 0;
    flex: 1; }
    .new-design2020 .list-of-clients li {
      padding-left: 24px;
      padding-right: 24px;
      text-align: center; }
      .new-design2020 .list-of-clients li img {
        margin-bottom: 32px;
        max-height: 60px;
        max-width: 180px;
        transition: .4s ease; }
  .new-design2020 .about-section_second-block-1 {
    padding-bottom: 26px;
    margin-bottom: 52px;
    position: relative; }
    .new-design2020 .about-section_second-block-1:before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 100px;
      height: 1px;
      background: #222222;
      opacity: .2; }
  .new-design2020 .about-section_second-block-2 {
    margin-bottom: 4px; }
    .new-design2020 .about-section_second-block-2 .about-section_second-block-2-info-title {
      font-weight: bold;
      font-size: 16px;
      line-height: 24px;
      color: #222222; }
    .new-design2020 .about-section_second-block-2 img {
      max-width: 450px; }
  .new-design2020 .about-section_second-block-2-info {
    padding-top: 17px;
    margin-top: 23px;
    position: relative; }
    .new-design2020 .about-section_second-block-2-info:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 70px;
      height: 1px;
      background: #BEA267; }
  .new-design2020 .about-section_second__img-1 {
    max-width: 237px;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-left: auto; }
    .new-design2020 .about-section_second__img-1 span {
      font-size: 14px;
      line-height: 18px;
      color: #555555;
      margin-top: 16px;
      display: block; }
    .new-design2020 .about-section_second__img-1 img {
      width: 100%; }
  @media (max-width: 1199.98px) {
    .new-design2020 .about-section_second-block-2 img {
      padding-right: 0 !important;
      margin-left: auto;
      margin-right: auto;
      display: block; } }
  @media (max-width: 767.98px) {
    .new-design2020 .about-section_second__img-1 {
      margin-right: auto;
      margin-top: 25px; } }

#footer {
  margin-top: auto; }

.about-section_first {
  margin-top: 29px; }

#article a,
#article a:active,
#article a:focus,
#article a:hover {
  color: #bea267; }

.article-faq-section {
  margin-bottom: 18px; }

#article-faq {
  list-style-type: none;
  padding-left: 0; }
  #article-faq li {
    margin-bottom: 22px;
    font-size: 14px;
    line-height: 24px;
    color: #555555; }
    #article-faq li p {
      font-size: 14px;
      line-height: 24px;
      color: #555555;
      margin-bottom: 24px; }
    #article-faq li a {
      color: #555555; }
      #article-faq li a:hover {
        color: #555555; }
    #article-faq li > div {
      margin-top: 22px; }
    #article-faq li:last-of-type {
      margin-bottom: 0; }
    #article-faq li > a {
      display: block;
      font-weight: bold;
      font-size: 16px;
      line-height: 24px;
      position: relative;
      padding-left: 38px; }
      #article-faq li > a:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        border: 1px solid #BEA267;
        display: flex;
        align-items: center;
        justify-content: center; }
      #article-faq li > a:after {
        content: '−';
        color: #BEA267;
        font-size: 20px;
        font-weight: 300;
        line-height: normal;
        position: absolute;
        left: 7px;
        top: 1px; }
      #article-faq li > a:hover, #article-faq li > a:focus, #article-faq li > a:active {
        text-decoration: none;
        color: #555555; }
      #article-faq li > a.collapsed:after {
        content: '+';
        left: 7px;
        top: 1px; }
